import React, { Fragment,useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import Pagination from 'react-js-pagination'
import Loader from './Loader'

import 'rc-slider/assets/index.css';
import MetaData from './MetaData'
import Product from './Product'
import {useDispatch,useSelector} from 'react-redux'
import Axios from './Axios';
import { getProducts } from '../actions/productActions'
import { useAlert } from 'react-alert'
import axios from 'axios';
import SliderView from './SliderView'
import CatHome from './CatHome';
import CatDemo from './CatDemo';

const Home = () => {
  const dispatch=useDispatch();
const [price, setPrice] = useState([1,100000])
  const alert=useAlert();
  const {loading,products,error,resPerPage}=useSelector(state=>state.products)
  const [category, setCategory] = useState('')
  const[rating,setRating]=useState(0)
 const[ther,setTHer]=useState();
  const {keyword}=useParams()
const [currentPage, setCurrentPage] = useState(1)

  useEffect(() => {
    if(error){
     return  alert.error(error);
    }
    dispatch(getProducts(keyword,currentPage,price,category,rating))
   
    
   
  }, [dispatch,alert,error,keyword,currentPage,price,category,rating])
  
function setCurrentPageNO(pageNumber){
setCurrentPage(pageNumber)
}
const scrollY=()=>{
  console.log("scrolling")
}

    return (
       <div className="HomePage" id='outer-container' >
        <CatDemo/>
    <div id='homeId'>
         {loading?<Loader/>:(
           <Fragment>
              <MetaData title={'Buy Best products Online'}/>
              <SliderView/>
           
      <div className="container container-fluid">
        
       <CatHome/>
      <section id="products" className="container mt-5">
   
      <h2 class="section-title position-relative text-uppercase mx-xl-5 mb-4"><span class="bg-secondary pr-3">Featured Products</span></h2>
      
<div className="row" >
  {
    products&& products.map(product=>(
      <Product key={product._id} product={product} />
      ))
  
}
     
        </div>
    </section>
        </div>
     
        <div className="d-flex justify-content-center mt-5">
          <Pagination
            activePage={currentPage}
            itemsCountPerPage={resPerPage}
            totalItemsCount={products&&products.length}
            onChange={setCurrentPageNO}
            itemClass="page-item"
            linkClass="page-link"
          />
        </div>
        
           </Fragment>
         )} 
</div>
        </div>
    )
}

export default Home
